.footer {
  width: 100%;
  height: 25px;
  background: #21325e;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
  position: absolute;
  bottom: 0;
  }

  .socialMedia svg {
    color: white;
    margin: 20px;
    font-size: 70px;
    cursor: pointer;
  }

  .footer p {
    color: white;
  }

  @media only screen and (max-width: 600px) {
    .footer svg {
      font-size: 50px;
    }
  }
